          src: require("./assets/Songs/尾崎豊　卒業 - Yutaka Ozaki - graduada GRADUATION.mp3"),
          cover: require("./assets/images/8.jpg"),
        },
        {
          title: "せんせい",
          artist: "森昌子",
          src: require("./assets/Songs/森昌子 せんせい（1986 - 06-29）.mp3"),
          cover: require("./assets/images/9.jpg"),
        },
        {
          title: "孤愁",
          artist: "森昌子",
          src: require("./assets/Songs/森昌子 孤愁人（1986 - 06-29）.mp3"),
          cover: require("./assets/images/10.jpg"),
        },
        {
          title: "歌詞付き milk tea",
          artist: "福山雅治",
          src: require("./assets/Songs/福山雅治 -歌詞付き milk tea.mp3"),
          cover: require("./assets/images/11.jpg"),
        },
        {
          title: "糸",
          artist: "中島みゆき",
          src: require("./assets/Songs/糸 - 中島みゆき.mp3"),
          cover: require("./assets/images/12.jpg"),
        },
        {
          title: "おやすみ",
          artist: "羊毛とおはな",
          src: require("./assets/Songs/羊毛とおはな - おやすみ.mp3"),
          cover: require("./assets/images/13.jpg"),
        },
        {
          title: "空が白くてさ",
          artist: "羊毛とおはな",
          src: require("./assets/Songs/羊毛とおはな - 空が白くてさ.mp3"),
          cover: require("./assets/images/14.jpg"),
        },
        {
          title: "いつでも夢を",
          artist: "吉永小百合・橋幸夫",
          src: require("./assets/Songs/いつでも夢を／吉永小百合・橋幸夫.mp3"),
          cover: require("./assets/images/15.jpg"),
        },
        {
          title: "别れの予感",
          artist: "テレサ・テン",
          src: require("./assets/Songs/テレサ・テン-别れの予感 - 128K MP3.mp3"),
          cover: require("./assets/images/16.jpg"),
        },
        {
          title: "冬の色",
          artist: "山口百恵ちゃん",
          src: require("./assets/Songs/冬の色／山口百恵ちゃん - 128K MP3.mp3"),
          cover: require("./assets/images/17.jpg"),
        },
        {
          title: "大きな古時計",
          artist: "平井堅",
          src: require("./assets/Songs/大きな古時計 - 平井堅 Ken Hirai.( RB 節奏藍調，特別版  ).mp3"),
          cover: require("./assets/images/18.jpg"),
        },
        {
          title: "思いがかさなるその前に",
          artist: "平井堅",
          src: require("./assets/Songs/平井堅 (Ken Hirai) - 思いがかさなるその前に (Before the feelings overlap) LIVE.mp3"),
          cover: require("./assets/images/19.jpg"),
        },
      ],
      player: new Audio(),
      transitionName: null,
    };
  },
  methods: {
    play(song) {
      if (typeof song.src != "undefined") {
        this.current = song;

        this.player.src = this.current.src;
      }
      this.player.play();
      this.isPlaying = true;
          this.titleAnimate();

    },
    pause() {
      this.player.pause();
      this.isPlaying = false;
    },
    next() {
      this.index++;
      if (this.index > this.songs.length - 1) {
        this.index = 0;
      }
      this.current = this.songs[this.index];
      this.play(this.current);
      this.transitionName = "scale-out";
    },
    prev() {
      this.index--;
      if (this.index < 0) {
        this.index = this.songs.length - 1;
      }

      this.transitionName = "scale-in";
    },
    replay() {
      this.player.pause();
      this.player.currentTime = 0;
      this.current = this.songs[this.index];
      this.play(this.current);
    },
    shuffle() {
      let min = 0;
      let max = this.songs.length - 1;
      let random = Math.floor(Math.random() * (+max - +min) + +min);
      this.index = random;
      this.current = this.songs[this.index];
      this.play(this.current);
    },
  titleAnimate() {
      var character = 22;
      var msg = this.current.title
      this.counter.timer = setInterval(function () {
        document.title =
          msg.substring(character, msg.length) + msg.substring(0, character);
        character++;
        if (character > msg.length) {
          character = 0;
        }
      }, 500);
    },
  },

  created() {
    this.current = this.songs[this.index];
    this.player.src = this.current.src;
  },
};
</script>

